.home img {
  box-shadow: 10px 9px 22px #257;
}

.card {
  transition: all 0.5s ease;
}

.card:hover {
  cursor: pointer;
  box-shadow: 0 .5rem 1rem rgba(0, 0, 0, .15);
}

.form {
  background: rgb(0, 101, 219);
  background: linear-gradient(90deg, rgb(73, 161, 177) 50%, rgba(0, 0, 0, 100%));
}

.footer {
  /* background: rgb(0, 101, 219); */
  /* background: linear-gradient(90deg, rgb(0, 101, 219) 10%, rgba(0, 212, 255, 1)100%); */
  background: linear-gradient(90deg, rgb(73, 161, 177) 50%, rgba(0, 0, 0, 100%));
}

.text-blue-color {
  color: paleturquoise
}

.theme-color {
  color: rgb(73, 161, 177)
}

.btn-theme {
  display: inline-block;
  font-weight: 400;
  line-height: 1.5;
  color: rgb(73, 161, 177);
  text-align: center;
  text-decoration: none;
  vertical-align: middle;
  cursor: pointer;
  -webkit-user-select: none;
  user-select: none;
  background-color: transparent;
  border: 1px solid rgb(73, 161, 177);
  padding: 0.375rem 0.75rem;
  font-size: 1rem;
  border-radius: 0.25rem;
  transition: color .15s ease-in-out, background-color .15s ease-in-out, border-color .15s ease-in-out, box-shadow .15s ease-in-out;
}

.btn-theme:hover {
  background-color: rgb(73, 161, 177);
  color: white;
}

/* sidebar */
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

.main-container {
  display: flex;
}

/* main {
  padding: 10px;
} */

.title {
  font-size: 3rem;
  display: grid;
  place-items: center;
}

/* Sidebar */
.sidebar {
  /* background: rgb(0, 7, 61); */
  background: rgb(73, 161, 177);
  color: white;
  /* height: 100vh; */
}

.top_section {
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 15px 10px;
}

.logo {
  font-size: 18px;
  line-height: 0;
}

.bars {
  width: 30px;
}

.hide {
  display: none;
}

.search {
  display: flex;
  align-items: center;
  margin: 10px 0;
  height: 30px;
  padding: 10px;
}

.search input {
  border: none;
  margin-left: 10px;
  border-radius: 5px;
  background: rgb(238, 238, 238);

  color: white;
}

.routes {
  margin-top: 15px;
  display: flex;
  flex-direction: column;
  gap: 5px;
}

.link {
  display: flex;
  color: white;
  gap: 10px;
  padding: 5px 10px;
  border-right: 4px solid transparent;
  transition: 0.2s cubic-bezier(0.6, -0.28, 0.735, 0.045);
}

.link:hover {
  border-right: 4px solid white;
  background: rgb(45, 51, 89);
  transition: 0.2s cubic-bezier(0.6, -0.28, 0.735, 0.045);
}

.side-active {
  border-right: 4px solid white;
  background: rgb(45, 51, 89);
}

.link_text {
  white-space: nowrap;
  font-size: 15px;
}

